.home-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.home-title {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #eee;
  padding-bottom: 10px;
}

/* Auth buttons container */
.auth-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
}

/* General button style */
.btn {
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 8px;
  text-decoration: none;
  transition: all 0.3s ease;
}

/* Login button */
.login-btn {
  background-color: #007bff;
  color: #fff;
}

.login-btn:hover {
  background-color: #0056b3;
}

/* Register button */
.register-btn {
  background-color: #28a745;
  color: #fff;
}

.register-btn:hover {
  background-color: #1e7e34;
}


/* Full page centering */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #f5f7fa;
  padding: 20px;
}

/* Card container */
.login-card {
  background: #ffffff;
  padding: 30px 40px;
  border-radius: 12px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

/* Title */
.login-title {
  font-size: 1.8rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

/* Form inputs (affects inputs inside LoginForm) */
.login-card input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

/* Button */
.login-card button {
  width: 100%;
  padding: 12px;
  margin-top: 15px;
  border: none;
  border-radius: 8px;
  background: #007bff;
  color: white;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s ease;
}

.login-card button:hover {
  background: #0056b3;
}



