.product-list-container {
  padding: 20px;
  max-width: 1200px;
  margin: auto;
}

.filter-bar {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.search-input {
  flex: 1;
  min-width: 250px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  outline: none;
  transition: 0.3s ease;
}

.search-input:focus {
  border-color: #007bff;
  box-shadow: 0px 0px 6px rgba(0, 123, 255, 0.3);
}

.category-select {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  background: white;
  cursor: pointer;
}

.category-select:hover {
  border-color: #007bff;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.no-products {
  text-align: center;
  font-size: 1.1rem;
  color: #888;
  grid-column: 1 / -1;
}


/* card */
.product-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 12px;
  overflow: hidden;
  width: 240px;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
}

.product-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.product-info {
  padding: 12px;
  text-align: center;
}

.product-name {
  font-size: 1rem;
  margin: 8px 0;
  color: #333;
}

.product-price {
  font-size: 1.1rem;
  font-weight: bold;
  color: #007bff;
  margin-bottom: 10px;
}

.view-btn {
  display: inline-block;
  padding: 8px 14px;
  font-size: 0.9rem;
  color: #fff;
  background: #007bff;
  border-radius: 6px;
  text-decoration: none;
  transition: background 0.2s ease;
}

.view-btn:hover {
  background: #0056b3;
}
@media (max-width: 768px) {
  .product-list-container {
    padding: 8px;
    max-width: 100%;
  }
  .filter-bar {
    gap: 8px;
    margin-bottom: 12px;
    flex-direction: column;
    align-items: stretch;
  }
  .search-input, .category-select {
    min-width: 0;
    font-size: 0.98rem;
    padding: 10px;
  }
  .products-grid {
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  .product-card {
    width: 100%;
    min-width: 0;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    margin: 0;
  }
  .product-image {
    height: 120px;
    border-radius: 0;
  }
  .product-info {
    padding: 8px;
  }
  .product-name {
    font-size: 0.98rem;
    margin: 6px 0;
  }
  .product-price {
    font-size: 1rem;
    margin-bottom: 8px;
  }
  .view-btn {
    padding: 8px 0;
    font-size: 0.95rem;
    width: 100%;
    border-radius: 4px;
  }
}
